export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			url: '../../static/images/mine-head-portrait.png',
			date: currentDate,
			file: [],
			// 存储手机号码
			phone: ''
		};
	},
	methods: {
		onLoad() {
			this.phone = uni.getStorageSync('phone')
		},
		// 当选择的日期发生改变的时候 改变页面的日期
		bindDateChange(e) {
			this.date = e.target.value
			uni.request({
				url: "http://192.168.1.143:8086/WNC/user/updateBirthday",
				data: {
					userId: 1,
					birthday: this.date
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
				},
				success: res => {
					console.log(res)
				}
			});
		},
		// 日期的格式
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 性别单选框发生改变的时候
		radioChange(e) {
			// console.log(e.detail.value)
			uni.request({
				url: "http://192.168.1.143:8086/WNC/user/updateGender",
				data: {
					userId: 1,
					gender: e.detail.value
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
				},
				success: res => {
					console.log(res)
				}
			});
		},
		// 去修改昵称页面
		goMineNickname() {
			uni.navigateTo({
				url: '/pages/mineNickname/mineNickname'
			})
		},
		// 去修改手机号码的验证码页面
		goMineVerificationCode() {
			uni.navigateTo({
				url: '/pages/mineVerificationCode/mineVerificationCode'
			})
		},
		// 点击更换用户头像
		replace() {				
			uni.chooseImage({
				count: 1,
				success: res => {
					console.log(res)
					this.file = res.tempFilePaths
					this.url = res.tempFilePaths[0]
					// var imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: 'http://192.168.1.143:8086/WNC/user/updateimageUrl',
						filePath: this.url,
						name: 'file',
						formData: {
								userId: 1
							},
						success: res => {
							console.log(res)
						}
					})
					// uni.request({
					// 	url: "http://192.168.1.143:8086/WNC/user/updateimageUrl",
					// 	data: {
					// 		file: this.file,
					// 		userId: 1
					// 	},
					// 	header: {
					// 		"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
					// 	},
					// 	success: res => {
					// 		console.log(res)
					// 	}
					// });
				}
			})
		}
	},
	computed: {
		// 日期的开始范围
		startDate() {
			return this.getDate('start');
		},
		// 日期的结束范围
		endDate() {
			return this.getDate('end');
		}
	}
};
